@media (min-width: 1024px) {

  /**
   * CUSTOM PROPERTY
   */

  :root {

    /**
    * shadow
    */

    --shadow-1: -4px 8px 24px hsla(0, 0%, 0%, 0.125);
    --shadow-2: 0 16px 30px hsla(0, 0%, 0%, 0.125);
    --shadow-3: 0 16px 40px hsla(0, 0%, 0%, 0.125);

  }


  /**
   * MAIN 
   */

  main { margin-bottom: 60px; }


  /* service */

  .service-list {
    grid-template-columns: 1fr 1fr;
    gap: 20px 25px;
  }

  /* testimonials */

  .testimonials-item { min-width: calc(50% - 15px); }

  /* clients */

  .clients-item { min-width: calc(25% - 38px); }

  /**
   * BLOG
   */

  .blog-banner-box { height: 230px; }

}







.sidebar-info_more {
  display: flex;
  align-items: center;
  max-width: 100%; 
  padding: 20px; 
  box-sizing: border-box;
  overflow: hidden;
  justify-content: center; /* İçerikleri yatay olarak ortalar */
}

.sidebar.active .sidebar-info_more {
  opacity: 1;
  visibility: visible;
}

.contacts-list {
  display: flex; /* Flexbox kullanarak öğeleri hizalar */
  flex-direction: column; /* Öğeleri dikey olarak hizalar */
  align-items: center; /* Öğeleri yatay olarak ortalar */
  gap: 16px;
}

.contact-item {
  width: 100%; /* Öğelerin genişliğini tam yapar */
  display: flex;
  align-items: center;
  gap: 16px;
  justify-content: center; /* İçerikleri yatay olarak ortalar */
}

.contact-info {
  max-width: calc(100% - 46px);
  width: calc(100% - 46px);
}

.contact-link {
  color: white;
}

.contact-title {
  color: var(--light-gray-70);
  font-size: var(--fs-8);
  text-transform: uppercase;
  margin-bottom: 2px;
}

.contact-info :is(.contact-link, time, address) {
  color: var(--white-2);
  font-size: var(--fs-7);
}

.contact-info a { 
  font-style: normal; 
}
