.select-item button {
  background: var(--eerie-black-2);
  color: var(--light-gray);
  font-size: var(--fs-6);
  font-weight: var(--fw-300);
  text-transform: capitalize;
  width: 100%;
  padding: 8px 10px;
  border-radius: 8px;
}

.select-item button:hover {
  --eerie-black-2: hsl(240, 2%, 20%);
}

.portfolio-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Default to 3 columns */
  gap: 30px;
  margin-bottom: 10px;
}

@media (max-width: 1200px) {
  .portfolio-list {
    grid-template-columns: repeat(2, 1fr); /* 2 columns for medium screens */
  }
}

@media (max-width: 768px) {
  .portfolio-list {
    grid-template-columns: 1fr; /* 1 column for small screens */
  }
}

.portfolio-item {
  border-radius: 16px;
  border: 3px solid var(--orange-yellow-crayola);
  display: none;
}

.portfolio-item.active {
  display: block;
  animation: scaleUp 0.25s ease forwards;
}

@keyframes scaleUp {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}

.portfolio-item > a {
  width: 100%;
}

.portfolio-img {
  position: relative;
  width: 100%;
  height: 200px;
  border-radius: 16px;
  overflow: hidden;
  margin-bottom: 15px;
}

.portfolio-img::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  z-index: 1;
  transition: var(--transition-1);
}

.portfolio-item > a:hover .portfolio-img::before {
  background: hsla(0, 0%, 0%, 0.5);
}

.portfolio-item-icon-box {
  --scale: 0.8;
  background: var(--jet);
  color: var(--orange-yellow-crayola);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(var(--scale));
  font-size: 20px;
  padding: 10px;
  border-radius: 12px;
  opacity: 0;
  z-index: 1;
  transition: var(--transition-1);
}

.portfolio-item > a:hover .portfolio-item-icon-box {
  --scale: 1;
  opacity: 1;
}

.portfolio-item-icon-box ion-icon {
  --ionicon-stroke-width: 50px;
}

.portfolio-img  {
  position: relative;
  width: 100%;
  
  border-radius: 16px;
  overflow: hidden;
  margin-bottom: 15px;
  box-sizing: border-box;
}

.portfolio-img img {
  position: absolute;
  top: 0;
  left: 0;
  align-items: center;  
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensures the image covers the container while maintaining its aspect ratio */
  transition: var(--transition-1);
}

.portfolio-item > a:hover img {
  transform: scale(1.1);
}

.portfolio-title,
.portfolio-category {
  text-align: center;
  padding-bottom: 10px;
}

.portfolio-title {
  color: var(--white-2);
  font-size: var(--fs-5);
  font-weight: var(--fw-400);
  text-transform: capitalize;
  line-height: 1.3;
}

.portfolio-category {
  color: var(--light-gray-70);
  font-size: var(--fs-6);
  font-weight: var(--fw-300);
}
