
.project-posts { margin-bottom: 10px; }

.project-posts-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

@media (max-width: 768px) {
  .project-posts-list {
    grid-template-columns: repeat(1, 1fr);
  }

}

@media (min-width:769px) and  (max-width: 1024px) {
  .project-posts-list {
    grid-template-columns: repeat(2, 1fr);
  }


}

.project-post-item > a {
  position: relative;
  background: var(--border-gradient-onyx);
  height: 100%;
  box-shadow: var(--shadow-4);
  border-radius: 16px;
  border: 3px solid var(--orange-yellow-crayola);
  z-index: 1;
}

.project-post-item > a::before {
  content: "";
  position: absolute;
  inset: 1px;
  border-radius: inherit;
  background: var(--eerie-black-1);
  z-index: -1;
}

.project-banner-box {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 16px;
  box-sizing: border-box;
}

.project-banner-box .figure {
  width: 70%;
  height: 70%;
  
}

.project-post-item > a:hover .project-banner-box img { transform: scale(1.1); }

.project-content { padding: 15px; }

.project-meta {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 7px;
  margin-bottom: 10px;
}

.project-meta :is(.project-category, time) {
  color: var(--light-gray-70);
  font-size: var(--fs-6);
  font-weight: var(--fw-300);
}

.project-meta .dot {
  background: var(--light-gray-70);
  width: 4px;
  height: 4px;
  border-radius: 4px;
}

.project-item-title {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 10px 0px 10px;
  line-height: 1.3;
  transition: var(--transition-1);
}

.project-post-item > a:hover .project-item-title { color: var(--orange-yellow-crayola); }

.project-text {
  color: var(--light-gray);
  font-size: var(--fs-6);
  font-weight: var(--fw-300);
  line-height: 1.6;
}